* {
  box-sizing: border-box;
   margin: 0;
  padding: 0;
}

body {
  font-family: 'Lato', sans-serif;
  overflow-x: hidden;
}

.container {
    margin: 0  auto;
    
}

.h-100 {
  height: 100vh;  
}
.row {
  display: flex;
  align-items: center ;
  justify-content: center;
}
.row .col-12 {
  width: 100%;
}
.row .col-6 {
  width: 50%;
} 

